import { SubHeader } from './../components'

const data = ['All', 'Fresh Wiring', 'Installation', 'Products', 'Repair', 'Ventures']

const imgData = Array(10).fill(null);

export default () => (
    <>
        <SubHeader 
            subHeader="Recent Works"
            invertColors         
        />
        <div className="flex flex-wrap content-between items-center text-center">
            {
                data.map((c, index) => <div className="text-2xl text-white mx-2 font-semibold" key={index}>{c}</div>)
            }
        </div>
        <div className="flex flex-wrap -mx-4">
            {
                imgData.map((item, index) => <div className="bg-white m-4 h-48 w-48"></div>)
            }
        </div>

    </>
)